<template>
  <div class="setting-view">
    <header-view :title="title" :fixed="fixed" :routerName="routerName"></header-view>
    <section class="set-contaniner">
      <div class="setting-list">
        <div class="setting-list-item" @click="setting(userRouter,name)"><span class="setting-label">昵称</span><span class="setting-more">{{name}}<i class="icon iconfont icon-more"></i></span></div>
        <div class="setting-list-item" @click="setting(pswRouter,psw)"><span class="setting-label">修改密码</span><span class="setting-more"><i class="icon iconfont icon-more"></i></span></div>
        <div class="setting-list-item" @click="setting(yhkRouter,yhk)"><span class="setting-label">我的银行卡</span><span class="setting-more"><i class="icon iconfont icon-more"></i></span></div>
      </div>
    </section>
    <footer class="login-out" @click="loginout">退出登录</footer>
  </div>
</template>

<script>
  import {getLogout} from '../../service/getData'
  import axios from 'axios'
  import header from '@/components/header/header'
  export default {
    components: {
      'header-view': header
    },
    data () {
      return {
        name: '',
        psw: '123456',
        yhk: '13688455785544555',
        title: '我的设置',
        fixed: false,
        userRouter: 'username',
        pswRouter: 'password',
        yhkRouter: 'bankcard',
        routerName: 'custom'

      }
    },
    created: function () {
      if (this.$route.params.userName) {
        localStorage.setItem('userName', this.$route.params.userName)
      }
      this.name = this.$route.params.userName === undefined ? localStorage.userName : this.$route.params.userName
    },
    methods: {
      setting: function (val, param) {
        this.$router.push({
          name: val,
          params: {
            pageTitle: '修改密码',
            routerName: 'userSet'
          }
        })
      },
      loginout: function () {
        var _this = this
        getLogout().then(function (rep) {
          if(rep.data.code == '1'){
            localStorage.removeItem('userMobile')
            _this.$router.push({
              name: 'login'
            })
          }else{
            _this.$toast('服务器返回数据失败')
          }
        }).catch(function () {
          _this.$toast('连接服务器失败')
        })
      }
      
    }
  }
</script>

<style>
/* 我的设置页面 */

.setting-view {
  height: 100%;
  display: flex;
  position: relative;
  flex-direction: column;
}

.setting-view .mint-header {
  -webkit-flex-shrink: 0;
  flex-shrink: 0;
}

.set-contaniner {
  height: 100%;
  flex: auto;
  background-color: #F5F5F5;
}

.setting-list {
  margin-top: .2rem;
  padding-left: .15rem;
  background-color: #FFFFFF;
}

.setting-list-item {
  clear: both;
  height: .55rem;
  color: #333;
  padding-right: .15rem;
  line-height: .55rem;
  border-bottom: .01rem solid #E5E5E5;
}

.setting-more {
  float: right;
  color: #999999;
}

.setting-more i {
  margin-left: .05rem;
  vertical-align: middle;
}

.login-out {
  position: absolute;
  bottom: .3rem;
  width: 3.03rem;
  height: .45rem;
  line-height: .45rem;
  left: 50%;
  text-align: center;
  color: rgb(255, 161, 38);
  transform: translateX(-50%);
  border-radius: .3rem;
  border: .01px solid rgb(255, 161, 38);
}
</style>


